import React, { ReactNode, Suspense } from "react";
import StorageService from "../../services/Storage.service";
import { useNavigate } from "react-router-dom";
import { Rings } from 'react-loader-spinner';


type TAuthGuard = {
    children?: ReactNode;
    auth: boolean;
}

const AuthGuard: React.FunctionComponent<TAuthGuard> = ({ children, auth }) => {

    /**
     * route manage
     */
    const navigate = useNavigate();

    (async function () {

        //storage 
        const STORE = new StorageService();
        const token = await STORE.getItem('token');

        /**
         * if has token
         */
        if (!token && auth) {
            navigate('/login');
        }

    })();


    return (<><Suspense fallback={<div className="flex flex-row justify-center mt-[20rem]"><Rings
        height="80"
        width="80"
        color="#4fa94d"
        radius="20"
        wrapperStyle={{}}
        wrapperClass=""
        visible={true}
        ariaLabel="rings-loading"
    /></div>}>{children} </Suspense></>);
};

export default AuthGuard;